@import "./normalize";
@import "./vars";
@import "./mixins";
@import "./fonts";
@import "./sprite_generated";
@import "./general";

// From libs:
//@import "../libs/LIB_NAME/path/to/style.scss"
// @import "../libs/swiper/swiper-bundle.min.css";

/*===== global styles =====*/
* {
    box-sizing: border-box;
    min-height: 0.001vw; // — Fixes Safari bug with viewport units in calc()
}

html {
    min-width: 320px;
}

body {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    min-width: 320px;
    display: flex;
    flex-direction: column;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    // font-family: $font_text;
    color: $color_dark1;
    // background-color: $color_white;
    font-family: $fonts_main;
    font-weight: 400;
}

body.lock,
body.noscroll {
    overflow: hidden;
}

.container {
    width: 100%;
    max-width: 1560px;
    min-width: 320px;
    margin: 0 auto;
    padding: 0 40px;

    @include lg {
        max-width: 1074px;
    }

    @include sm {
        padding: 0 30px;
    }
}

a {
    color: inherit;
    text-decoration: none;
    outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    vertical-align: baseline;
}

ol,
ul {
    list-style: none;
}

button,
input {
    padding: 0;
    font-family: inherit;
    font-size: inherit;
    border: none;
    border-radius: 0;
    appearance: none;
    outline: none;
    color: inherit;
    background-color: transparent;
}

input,
a,
button {
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
    -webkit-tap-highlight-color: transparent;
}

button {
    cursor: pointer;
}

textarea {
    border-radius: 0;
}

:active,
:hover,
:focus {
    outline: 0;
    outline-offset: 0;
}
