@font-face {
  font-family: "Geologica";
  src: local("Geologica Light"), local("Geologica-Light"), url("../fonts/Geologica-Light.woff2") format("woff2"), url("../fonts/Geologica-Light.woff") format("woff"), url("../fonts/Geologica-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: "Geologica";
  src: local("Geologica Regular"), local("Geologica-Regular"), url("../fonts/Geologica-Regular.woff2") format("woff2"), url("../fonts/Geologica-Regular.woff") format("woff"), url("../fonts/Geologica-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Geologica";
  src: local("Geologica Medium"), local("Geologica-Medium"), url("../fonts/Geologica-Medium.woff2") format("woff2"), url("../fonts/Geologica-Medium.woff") format("woff"), url("../fonts/Geologica-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Nominee";
  src: url("../fonts/Nominee-MediumItalic.woff2") format("woff2"), url("../fonts/Nominee-MediumItalic.woff") format("woff"), url("../fonts/Nominee-MediumItalic.ttf") format("truetype");
  font-weight: 500;
  font-style: italic;
  font-display: swap;
}
/*===== global styles =====*/
* {
  box-sizing: border-box;
  min-height: 0.001vw;
}

html {
  min-width: 320px;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2b2922;
  font-family: "Geologica", sans-serif;
  font-weight: 400;
}

body.lock,
body.noscroll {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1560px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1300px) {
  .container {
    max-width: 1074px;
  }
}
@media (max-width: 991px) {
  .container {
    padding: 0 30px;
  }
}

a {
  color: inherit;
  text-decoration: none;
  outline: none;
}

html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul,
li {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}

ol,
ul {
  list-style: none;
}

button,
input {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 0;
  appearance: none;
  outline: none;
  color: inherit;
  background-color: transparent;
}

input,
a,
button {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-tap-highlight-color: transparent;
}

button {
  cursor: pointer;
}

textarea {
  border-radius: 0;
}

:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}

.scr_header {
  position: relative;
  z-index: 4;
  padding: 35px 0;
  background-color: #fff;
  border-bottom: 2px solid #f6f6f2;
}
@media (max-width: 1600px) {
  .scr_header {
    padding: 33px 0;
  }
}
@media (max-width: 1300px) {
  .scr_header {
    padding: 31px 0;
  }
}
@media (max-width: 991px) {
  .scr_header {
    padding: 26px 0;
  }
}
@media (max-width: 767px) {
  .scr_header {
    padding: 25px 0 20px;
  }
}
.scr_header.fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.scr_header__container {
  max-width: 1880px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
}
@media (max-width: 991px) {
  .scr_header__container {
    padding: 0 30px;
  }
}

.scr_header__logo {
  display: flex;
  align-items: center;
  gap: 0 60px;
  cursor: pointer;
}
.scr_header__logo img {
  display: block;
  width: 145px;
  height: auto;
}
.scr_header__logo span {
  white-space: nowrap;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #c5c5bb;
}
@media (max-width: 1300px) {
  .scr_header__logo {
    gap: 0 56px;
  }
  .scr_header__logo img {
    width: 136px;
  }
  .scr_header__logo span {
    font-size: 18px;
    letter-spacing: 0.06em;
  }
}
@media (max-width: 991px) {
  .scr_header__logo {
    gap: 0 29px;
  }
  .scr_header__logo img {
    width: 113px;
  }
  .scr_header__logo span {
    font-size: 15px;
  }
}
@media (max-width: 767px) {
  .scr_header__logo {
    flex-direction: column;
    gap: 2px 0;
  }
  .scr_header__logo img {
    width: 167px;
  }
  .scr_header__logo span {
    display: none;
  }
}

.scr_header__phone {
  position: relative;
  font-weight: 500;
  font-size: 20px;
  letter-spacing: 0.03em;
  color: #2b2922;
  transform: translateX(-25px);
}
.scr_header__phone::after {
  content: "";
  position: absolute;
  display: block;
  bottom: -9px;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #7e915f;
  transition: transform 0.2s ease-out;
}
.scr_header__phone span:nth-child(2) {
  display: none;
}
@media (hover: hover) {
  .scr_header__phone:hover::after {
    transform: scaleX(1.1);
  }
}
@media (max-width: 1600px) {
  .scr_header__phone {
    transform: translateX(-3px);
  }
}
@media (max-width: 1300px) {
  .scr_header__phone {
    font-size: 18px;
    text-align: right;
    transform: translateX(0);
  }
}
@media (max-width: 991px) {
  .scr_header__phone {
    font-size: 15px;
  }
}
@media (max-width: 767px) {
  .scr_header__phone::after {
    bottom: -2px;
    height: 2px;
  }
  .scr_header__phone span:nth-child(1) {
    display: none;
  }
  .scr_header__phone span:nth-child(2) {
    display: block;
    font-size: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
  }
}

.scr_header__progress {
  position: absolute;
  bottom: -8px;
  left: 0;
  width: 100%;
  height: 8px;
  overflow: hidden;
}
.scr_header__progress span {
  display: block;
  height: 8px;
  background-color: #7e915f;
  width: 0;
  transition: width 0.4s ease-out;
}
@media (max-width: 991px) {
  .scr_header__progress {
    height: 6px;
  }
  .scr_header__progress span {
    height: 6px;
  }
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.scr_quiz {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  padding-top: 103px;
  overflow-x: hidden;
}
@media (max-width: 1600px) {
  .scr_quiz {
    padding-top: 93px;
  }
}
@media (max-width: 1300px) {
  .scr_quiz {
    padding-top: 100px;
  }
}
@media (max-width: 991px) {
  .scr_quiz {
    padding-top: 74px;
  }
}
@media (max-width: 767px) {
  .scr_quiz {
    padding-top: 54px;
  }
}
.scr_quiz > .container {
  flex-shrink: 0;
}

.mobile_only {
  display: none;
}
@media (max-width: 767px) {
  .mobile_only {
    display: block;
  }
}

.scr_quiz__title {
  margin-bottom: 5px;
  font-weight: 300;
  font-size: 94px;
  line-height: 1;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: #413e34;
  text-align-last: left;
}
@media (max-width: 1600px) {
  .scr_quiz__title {
    margin-bottom: 10px;
    font-size: 80px;
  }
}
@media only screen and (max-width: 1380px) {
  .scr_quiz__title {
    font-size: 70px;
  }
}
@media only screen and (max-width: 1380px) and (min-width: 1301px) {
  .scr_quiz__title {
    font-size: 70px;
    font-size: calc(0.1265822785 * 100vw + -94.6835443038px);
  }
}
@media only screen and (max-width: 1380px) and (min-width: 1380px) {
  .scr_quiz__title {
    font-size: 80px;
  }
}
@media (max-width: 1300px) {
  .scr_quiz__title {
    margin-bottom: 7px;
    font-size: 62px;
  }
}
@media only screen and (max-width: 1080px) {
  .scr_quiz__title {
    text-align: left;
    font-size: 56px;
  }
}
@media only screen and (max-width: 1080px) and (min-width: 992px) {
  .scr_quiz__title {
    font-size: 60px;
    font-size: calc(0.0681818182 * 100vw + -11.6363636364px);
  }
}
@media only screen and (max-width: 1080px) and (min-width: 1080px) {
  .scr_quiz__title {
    font-size: 62px;
  }
}
@media (max-width: 991px) {
  .scr_quiz__title {
    font-size: 48px;
  }
}
@media only screen and (max-width: 820px) {
  .scr_quiz__title {
    font-size: 40px;
  }
}
@media only screen and (max-width: 820px) and (min-width: 768px) {
  .scr_quiz__title {
    font-size: 44px;
    font-size: calc(0.1538461538 * 100vw + -78.1538461538px);
  }
}
@media only screen and (max-width: 820px) and (min-width: 820px) {
  .scr_quiz__title {
    font-size: 48px;
  }
}
@media (max-width: 767px) {
  .scr_quiz__title {
    margin-bottom: 15px;
    font-size: 32px;
  }
}

.scr_quiz__info {
  display: grid;
  grid-template-columns: auto 1fr;
  grid-template-areas: "current old" "current cta";
  gap: 0 30px;
}
@media (max-width: 1600px) {
  .scr_quiz__info {
    gap: 0 34px;
  }
}
@media (max-width: 767px) {
  .scr_quiz__info {
    grid-template-areas: "current time" "current old" "cta cta";
    gap: 0 14px;
  }
}

.scr_quiz__current {
  grid-area: current;
}

.scr_quiz__time {
  grid-area: time;
}

.scr_quiz__old {
  grid-area: old;
}

.scr_quiz__cta {
  grid-area: cta;
}

.scr_quiz__current {
  font-weight: 300;
  font-size: 120px;
  line-height: 1;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #7e915f;
}
@media (max-width: 1600px) {
  .scr_quiz__current {
    font-size: 100px;
  }
}
@media (max-width: 1300px) {
  .scr_quiz__current {
    font-size: 80px;
  }
}
@media (max-width: 991px) {
  .scr_quiz__current {
    font-size: 60px;
  }
}
@media (max-width: 767px) {
  .scr_quiz__current {
    font-size: 64px;
  }
}
@media only screen and (max-width: 370px) {
  .scr_quiz__current {
    font-size: 58px;
  }
}

.scr_quiz__time {
  display: none;
}
@media (max-width: 767px) {
  .scr_quiz__time {
    display: block;
    padding-top: 7px;
    font-weight: 500;
    font-size: 10px;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #c5c5bb;
  }
}

.scr_quiz__old {
  position: relative;
  display: inline-block;
  width: max-content;
  margin-bottom: 3px;
  padding-top: 10px;
  font-weight: 300;
  font-size: 60px;
  line-height: 1;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #c5c5bb;
}
.scr_quiz__old::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 55%;
  transform: translateY(-50%) rotate(-11deg);
  width: 100%;
  height: 5px;
  background-color: #c5c5bb;
}
@media (max-width: 1600px) {
  .scr_quiz__old {
    margin-bottom: 0;
    padding-top: 7px;
    font-size: 50px;
  }
  .scr_quiz__old::after {
    top: 50%;
  }
}
@media (max-width: 1300px) {
  .scr_quiz__old {
    font-size: 40px;
  }
}
@media (max-width: 991px) {
  .scr_quiz__old {
    font-size: 30px;
  }
}
@media (max-width: 767px) {
  .scr_quiz__old {
    font-size: 40px;
  }
}
@media only screen and (max-width: 370px) {
  .scr_quiz__old {
    font-size: 30px;
  }
}

.scr_quiz__cta {
  font-weight: 400;
  font-size: 20px;
  line-height: 120%;
  letter-spacing: 0.02em;
  text-transform: uppercase;
  color: #413e34;
}
.scr_quiz__cta span {
  position: relative;
