.scr_quiz__curve {
  position: absolute;
  bottom: 0;
  right: 10.9%;
  z-index: 2;
  width: 22.97%;
  max-width: 441px;
  min-width: 390px;
}
.scr_quiz__curve::after {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 139.68%;
}
.scr_quiz__curve::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
